#root {
  display: flex;
  flex-direction: column;
  min-width: 100vw;
}
.page_container {
  align-self: center;
  display: flex;
  flex-direction: column;
  /* align-items: center; */
  justify-content: center;
  width: 85%;
}
@media (min-width: 1440px) {
  .page_container {
    width: 1240px;
  }
}

@media (max-width: 871px) {
  .page_container {
    width: 89.4%;
  }
}

.main_logement {
  width: 100%;
}
